main
{

    a 
    {
        text-decoration: none;
    }
    a:hover
    {
        text-decoration: underline;
    }
    a:visited
    {
        //color: #1A0DAB;
        color :   #457bfb;
        
    }
    .Heading
    {
        margin-top: 2rem;

        text-align: center;
        .Heading-title
        {
            font-size: xx-large;
            hyphens: none;
            overflow-wrap:break-word;

            .Nonbreak
            {
                display: inline-block;
            }

            margin-bottom: 0rem;
        }

        .Heading-author
        {
            margin: 0.1rem 0rem;
            font-size: large;
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
        }

        .Heading-affiliation
        {
            color: #666666;
            margin: 0rem;
        }

        .Heading-submissions
        {
            font-size: large;
            text-decoration: underline;
        }
        .Heading-images
        {
            margin: 1.5rem 0rem;
            display: flex;          
            max-width: 100%;
            width: 100%; 
            height: auto;
            justify-content: center;
        }

        Img
        {
            max-width: 50%;
            height: auto;
            margin: 0rem 0.2rem;
        }
    }
    
    h2
    {
        color: #00a497;
        //color: #007f89;
        //color: #A05359;
    }
    ul
    {
        line-height: 2;
    }
    hr
    {
        height: 1px;
        margin: 0rem 5rem;
        background-color: #d3d3d3;
        //background-color: #f1e1e1;
        border: none;
    }
    div
    {
        margin: 0rem 5rem;
        hyphens: auto;
        line-height: 1.5;
    }

    .Abstract, .Acknowledgement
    {
        text-align: justify;
    }
    .SupplementalVideo 
    {
        .SupVideo
        {
            margin-bottom: 1rem;
            text-align: center;
        }
    }
}

footer
{
    margin: 1rem 5rem;

    font-size: small;
    text-align: center;
    //background-color: #fff8ff;
    padding: 1rem 0rem 0rem 0rem;
    //margin: 2rem 0 0 0;
    //width: 100%;

    border-top: 1px solid #d3d3d3;
}
